import React from "react";
import "./WorkFeature.css";
import Arrow from "./Arrow.svg";
import Coin from "./Coin.png";
import Dabur from "./Dabur.png";
import Hyundai from "./Hyundai.png";
import IBM from "./IBM.png";
import Jaguar from "./Jaguar.png";
import Reliance from "./Reliance.png";
import SBI from "./SBI.png";
import Wipro from "./Wipro.png";

function WorkFeature() {
  return (
    <div className="snapscroll">
      <div>
        <h3 className="workcontainer">
          <p>
            Our <span id="featurecolor"> Work Feature In</span>
          </p>
        </h3>
      </div>
      <div className="scrollableworkcontainer">
        <div className="leftarrow">
          <div className="arrow">
            <img src={Arrow} alt="Arrow" />
          </div>
        </div>
        <div className="workimgcontainer">
          <img src={IBM} alt="IBM" />
          <img src={Jaguar} alt="Jaguar" />
          <img src={Coin} alt="Coin" />
          <img src={SBI} alt="SBI" />
          <img src={Dabur} alt="Dabur" />
          <img src={Hyundai} alt="Hyundai" />
          <img src={Wipro} alt="Wipro" />
          <img src={Reliance} alt="Reliance" />
        </div>
        <div className="rightarrow">
          <div className="arrow">
            <img src={Arrow} alt="Arrow" />
          </div>
        </div>
      </div>
      <div className="extraspace"></div>
    </div>
  );
}

export default WorkFeature;
