import React from "react";
import "./Strength.css";
import StrengthCard from "./StrengthCard";
import Users from "./Users.png";
import Folder from "./Folder.png";
import Smile from "./Smile.png";

function Strength() {
  return (
    <div>
      <div className="strengthcontainer">
        <h3>
          <p>
            Our <span id="strength">Strength</span>
          </p>
        </h3>
      </div>
      <div className="strengthcardcontainer">
        <div id="topcarddiv">
          <StrengthCard
            title="1000+"
            content="Customer Worldwide"
            img={Users}
          />
        </div>
        <div className="bottomcardcontainer">
          <StrengthCard title="150+" content="IT Professionals" img={Smile} />
          <StrengthCard title="500+" content="Project Executed" img={Folder} />
        </div>
      </div>
      <div className="strengthextraspace"></div>
    </div>
  );
}

export default Strength;
