import React from "react";
import "./Card.css";
import Arrow from "../WorkFeature/Arrow.svg";
function Card(props) {
  return (
    <div className="dynacard">
      <div className="dynacardleft">
        <img src={props.img} alt={props.alt} />
      </div>
      <div className="dynacardright">
        <h3 className="dcardtitle">{props.title}</h3>
        <p className="dcardcontent">{props.content}</p>
      </div>
      <div className="slider">
        <div>
          <img src={Arrow} alt="arrow" />
        </div>
      </div>
    </div>
  );
}
export default Card;
