import React from "react";
import "./Excel.css";
import Meditation from "./Meditation.png";
import Successcard from "./Successcard";

function Excel() {
  return (
    <div>
      <div className="excelheader">
        <h3>
          <p>
            How we clain to <span id="excel">excel ?</span>
          </p>
        </h3>
      </div>
      <div className="successstories">
        <div className="successcontent">
          <div>
            <h3 className="successheading">17</h3>
            <p>Years in the field</p>
          </div>
          <div>
            <div className="successmiddlecolumn">
              <h3>Read further to</h3>
              <h3>our Success Stories</h3>
              <button>Let's talk</button>
            </div>
          </div>
          <div>
            <img src={Meditation} alt="" />
          </div>
        </div>
      </div>
      <div className="successcards">
        <div className="successcardlayout">
          <Successcard
            title="01"
            subtitle="Innovation"
            content="Continuously innovating products and solution to industry demand"
          />
          <Successcard
            title="02"
            subtitle="World Class Product"
            content="Continuously innovating products and solution to industry demand"
          />
          <Successcard
            title="03"
            subtitle="Zero risk"
            content="Work with customer to achieve zero risk on investment"
          />
          <Successcard
            title="04"
            subtitle="Tech savvy"
            content="Product is backed by world class technology team for most efficient realization"
          />
        </div>
      </div>
    </div>
  );
}

export default Excel;
