import React from "react";
import "./Footer.css";
import Location from "./Location.png";
import Mail from "./Mail.png";
import Telephone from "./Telephone.png";

function Footer() {
  return (
    <div className="lowestcontainer">
      <div className="upper">
        <div>
          <div className="infocontainer">
            <h3>Don’t miss our monthly newsletters with the best analysis.</h3>
            <div className="emailcontainer">
              <span id="email">Enter Your E-mail</span>
              <input type="text" placeholder="Demo@gmail.com" />
            </div>
            <button className="subscribe">SUBSCRIBE</button>
          </div>
        </div>
      </div>
      <div className="lower">
        <div className="lowercardholder">
          <div className="lowercards">
            <h3>Company</h3>
            <p>Company</p>
            <p>Carrer</p>
            <p>Blog</p>
            <p>Case Study</p>
            <p>Contact Us</p>
          </div>
        </div>
        <div className="lowercardholder">
          <div className="lowercards">
            <h3>Products</h3>
            <p>Plastic Cards</p>
            <p>Animal Tags</p>
            <p>RFID Readers</p>
            <p>Handheld Readers</p>
            <p>Biometric Devices</p>
            <p>Smart Cards</p>
            <p>Access Control System</p>
          </div>
        </div>
        <div className="lowercardholder">
          <div className="lowercards">
            <h3>Solutions</h3>
            <p>Campus Automation</p>
            <p>Asset Management</p>
            <p>Library Automation</p>
            <p>Livestock Management</p>
            <p>Event Management</p>
            <p>Laundry Management</p>
          </div>
        </div>
        <div className="lowercardholder">
          <div className="lowercards">
            <h3>Contact Us</h3>
            <div className="info">
              <img src={Location} alt="Location" />
              <p>Lane No. 5 Post Office Road CTOWN D.dun</p>
            </div>
            <div className="info">
              <img src={Telephone} alt="Telephone" />
              <p>+91-8923302446</p>
            </div>
            <div className="info">
              <img src={Telephone} alt="Telephone" />
              <p>+91-7830220159</p>
            </div>
            <div className="info">
              <img src={Mail} alt="Mail" />
              <p>byrxarpitsingh@gmail.com</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Footer;
