import "./nav.css";

import React from "react";

function Nav() {
  const handlelinks = (e) => {
    e.preventDefault();
  };
  return (
    <div className="navbar">
      <h3 id="logo">LOGO</h3>
      <div className="navlinks">
        <a href="void" onClick={handlelinks}>
          Products
        </a>
        <a href="void" onClick={handlelinks}>
          Application
        </a>
        <a href="void" onClick={handlelinks}>
          Case Study
        </a>
        <a href="void" onClick={handlelinks}>
          About Us
        </a>
        <a href="void" onClick={handlelinks} className="CTA">
          Contact Us
        </a>
      </div>
    </div>
  );
}

export default Nav;
