import React from "react";
import Card from "./Card";
import "./Solutions.css";

import Wallet from "./Wallet.png";
import Hotel from "./Hotel.png";
import GiftCard from "./GiftCard.png";
import Compass from "./Compass.png";

function Solutions() {
  return (
    <div>
      <div className="solutioncontainer">
        <h3>
          <p>
            Our <span id="solutioncolor">Solutions</span>
          </p>
        </h3>
      </div>
      <div className="maintaingridflow">
        <div className="cardcontainer">
          <Card
            title="E-Persue Application"
            content="E-Wallet Application for all type of electronic cash transaction, no warry about carry cash."
            img={Wallet}
          />
          <Card
            title="Gift Cards"
            content="plastic gift cards, e-gift card , corporate card solution for branding and loyalty."
            img={GiftCard}
          />
          <Card
            title="Compas Management"
            content="Project best image based on robust control on admin processes, attract best student, and produce best results."
            img={Compass}
          />
          <Card
            title="Hospital Management"
            content="Enables health care provider to reliably track hospital supplies, medical equipment and patient records with zero."
            img={Hotel}
          />
        </div>
      </div>
      <div className="solutionbuttoncontainer">
        <button>See Our All Solution</button>
      </div>
    </div>
  );
}
export default Solutions;
