import React from "react";
import "./Successcard.css";

function Successcard(props) {
  return (
    <div className="successcardholder">
      <h3 className="scardtitle">{props.title}</h3>
      <h5 className="scardsubtitle">{props.subtitle}</h5>
      <p className="scardcontent">{props.content}</p>
    </div>
  );
}

export default Successcard;
