import React from "react";
import "./Strengthcard.css";

function StrengthCard(props) {
  return (
    <div>
      <div className="strengthcard">
        <div className="strengthleft">
          <img src={props.img} alt={props.alt} />
        </div>
        <div className="strengthright">
          <div className="strengthtitle">{props.title}</div>
          <div className="strengthcontent">{props.content}</div>
        </div>
      </div>
    </div>
  );
}

export default StrengthCard;
