import React from "react";
import "./OurProducts.css";
import Cardreader from "./CardReader.svg";
import Cards from "./Cards.svg";
import Printer from "./Printer.svg";
import Doodleleft from "./doodleleft.png";
import Doodleright from "./doodleright.png";

function OurProducts() {
  return (
    <div>
      <div className="productheader">
        <h3>
          Our <span id="productscolor">Products</span>
        </h3>
      </div>
      <div className="productgrid">
        <div className="gridrow">
          <div className="card">
            <img src={Printer} alt="" />
            <h3>Card Reader</h3>
            <p>Contactless/Contact card</p>
            <p>Reader For Micro USB , </p>
            <p>Audio Jack, Mobile and USB</p>
          </div>
        </div>
        <div className="middlecolumn">
          <div className="card">
            <img src={Cards} alt="" />
            <h3>Plastic Cards</h3>
            <p>Premium Quality PVC</p>
            <p>Blank Card</p>
          </div>
          <button id="seeproductbutton">See Our All Products</button>
        </div>
        <div className="gridrow">
          <div className="card">
            <img src={Cardreader} alt="" />
            <h3>Magstripe Reader</h3>
            <p>Erroe Free Connects with </p>
            <p>PC via USB, Mobile via</p>
          </div>
        </div>
      </div>
      <div className="doodlecontainer">
        <img src={Doodleleft} className="leftdoodle" alt="left doodle" />
        <img src={Doodleright} className="rightdoodle" alt="right doodle" />
      </div>
    </div>
  );
}

export default OurProducts;
